<template>
  <app-content-wrapper :pageTitle="pageTitle">
    <div class="write__wrapper">
      <ui-form :name="'savePostForm'"
               :ref="'savePostForm'"
               :class="'write__frm'"
               :preventEnter="true"
               @onsubmit="onSubmit"
      >
        <div class="write__save-wrapper">
          <ui-button :type="'button'"
                     :color="'primary'"
                     :class="'write__btn--load'">
          </ui-button>
    
          <ul class="write__save-list"></ul>
        </div>

        <ui-hidden-field :name="'id'" :id="'savePostId'" :value="post?.id"></ui-hidden-field>
    
        <ui-write-table :name="'  '">
          <tr>
            <th scope="row">
              <label for="savePostTitle"></label>
            </th>
            <td>
              <div class="write__title">
                <ui-select :name="'category'"
                           :id="'savePostCategory'"
                           :class="'write__select-category'"
                           :title="' '"
                           :defaultValue="' '"
                           :rules="'required'"
                           :data="categoryList"
                           v-model="categoryId">
                </ui-select>
  
                <ui-text-field :type="'text'"
                               :name="'title'"
                               :id="'savePostTitle'"
                               :rules="'required'"
                               :block="true">
                </ui-text-field>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostCont"></label>
            </th>
            <td>
              <ui-textarea :name="'cont'"
                           :id="'savePostCont'"
                           :cols="'80'"
                           :rows="'18'"
                           :rules="'required'"
                           :hidden="true"
                           v-model="cont">
              </ui-textarea>

              <md-editor :language="'en-US'"
                         v-model="cont">
              </md-editor>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgDesc"> </label>

              <ui-icon-button :type="'button'"
                              :icon="'xi-refresh'"
                              :text="'  '"
                              :class="'write__og-desc-refresh'"
                              @click="changeOgDesc">
              </ui-icon-button>
            </th>
            <td>
              <ui-text-field :type="'text'"
                             :name="'ogDesc'"
                             :id="'savePostOgDesc'"
                             :class="'write__og-desc'"
                             :placeholder="'50 ( ,  )'"
                             :block="true">
              </ui-text-field>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgImgFile"> </label>
            </th>
            <td>
              <ui-hidden-field :name="'ogImg'" :id="'savePostOgImg'" :value="post?.avatarImg"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgUrl'" :id="'savePostOgImgUrl'" :value="post?.avatarImgUrl"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgSize'" :id="'savePostOgImgSize'" :value="post?.avatarImgSize"></ui-hidden-field>

              <ui-file-field :name="'ogImgFile'"
                             :id="'savePostOgImgFile'"
                             :accept="'image/*'"
                             :gap="10"
                             @onchange="onChangeOgImg">
                
                <ui-file-button :type="'button'"
                                :color="'secondary'"
                                @clickFile="onClickFile">Cloudinary
                </ui-file-button>
              </ui-file-field>

              <div class="blog-config__avatar-image-use-wrapper" v-if="post?.ogImg">
                <span class="blog-config__avatar-image-use">
                  {{ ogImg }} ( : {{ getFileSize(ogImgSize) }})
                </span>

                <ui-checkbox :name="'delOgImg'"
                             :id="'savePostDelOgImg'"
                             :label="''"
                             :values="'Y,N'">
                </ui-checkbox>
              </div>
            </td>
          </tr>
          <tr v-if="'post' === $route.meta.type">
            <th scope="row">
              <label for="savePostAddTag"></label>
            </th>
            <td>
              <ui-hidden-field :name="'saveTagList'" :id="'saveTagList'"></ui-hidden-field>
            
              <div class="write__tag__wrapper">
                <ui-text-field :type="'text'"
                               :name="'addTag'"
                               :id="'savePostAddTag'"
                               :class="'write__tag'"
                               :placeholder="'  ( , 5  )'"
                               :block="true"
                               v-model="tagStr">
                </ui-text-field>

                <ui-select :name="'tag'"
                           :id="'savePostTag'"
                           :class="'write__select-tag'"
                           :title="' '"
                           :defaultValue="' '"
                           :data="tagList"
                           @onchange="onChangeTag">
                </ui-select>
              </div>
            </td>
          </tr>
          <tr v-if="'post' === $route.meta.type">
            <th scope="row">/</th>
            <td>
              <ui-radio :id="'savePostSecretN'"
                        :name="'secretYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'N'"
                        v-model="secretYn">
              </ui-radio>
          
              <ui-radio :id="'savePostSecretY'"
                        :name="'secretYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="secretYn">
              </ui-radio>
            </td>
          </tr>
          <tr v-if="'post' === $route.meta.type">
            <th scope="row"></th>
            <td>
              <ui-radio :id="'savePostPinN'"
                        :name="'pinYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'N'"
                        v-model="secretYn">
              </ui-radio>

              <ui-radio :id="'savePostPinY'"
                        :name="'pinYn'"
                        :label="''"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="secretYn">
              </ui-radio>
            </td>
          </tr>

          <template v-slot:btn>
            <ui-button :type="'button'"
                       :color="'success'"
                       :class="'write__btn'">
            </ui-button>

            <ui-button :type="'reset'"
                       :color="'secondary'"
                       :class="'write__btn'">
            </ui-button>

            <ui-button :type="'submit'"
                       :color="'primary'"
                       :class="'write__btn'">
            </ui-button>
          </template>
        </ui-write-table>
      </ui-form>
    </div>
  </app-content-wrapper>
</template>

<script>
import { arrayHasDuplicateValue, isBlank, isNotEmpty, messageUtil } from '@/utils';
import { breadcrumbService } from '@/services/breadcrumb/breadcrumbService';
import AppSavePostTag from '@/components/views/post/AppSavePostTag.vue';

import MdEditor from 'md-editor-v3';
import 'md-editor-v3/lib/style.css';

export default {
  name: 'app-add-post',
  components: {
    AppSavePostTag,
    MdEditor,
  },
  data() {
    return {
      /**   */
      pageTitle: ' ',
      /**   */
      post: null,
      /**    */
      cont: '',
      /**   */
      ogImg: '',
      /**    */
      ogImgSize: '',
      /**    */
      ogImgFileList: [],
      /**    */
      categoryList: [],
      /**    ID */
      categoryId: '',
      /**    */
      tagList: [],
      /**    */
      secretYn: 'N',
      /**    */
      pinYn: 'N',
      /**  /     */
      tagStr: '',
      /**  /     */
      saveTagList: [],
    }
  },
  /**         */
  async beforeRouteLeave(to, from) {
    if ('/a/goto' === to.path) return true;

    const confirm = await messageUtil.confirmWarning('  ?', '     .');
    if (!confirm) return false;
  },
  created() {
    //   
    breadcrumbService.setPageTitle(this.pageTitle);

    this.init();
  },
  methods: {
    /**   */
    async init() {
      await Promise.all([
        this.listCategory(),
        this.listTag(),
      ]);
    },
    /**   */
    onSubmit(values) {
      this.setTagArr(values);
      
      const isValid = this.validationCheck();
      if (!isValid) return;
      
      console.log(values);

      const headers = { 'Content-Type': 'multipart/form-data' };
    },
    /**      */
    async changeOgDesc() {
      const title = this.$refs['savePostForm'].getFieldValue('title');

      if (isBlank(title)) {
        messageUtil.toastWarning(' .');
        return;
      }

      const confirm = await messageUtil.confirmSuccess('   ?');
      if (!confirm) return;

      this.$refs['savePostForm'].setFieldValue('ogDesc', title);
    },
    /** cloudinary    */
    onClickFile(file) {
      this.$refs['savePostForm'].setFieldValue('ogImg', file.public_id);
      this.$refs['savePostForm'].setFieldValue('ogImgUrl', file.url);
      this.$refs['savePostForm'].setFieldValue('ogImgSize', file.bytes);
    },
    /**    file input    */
    onChangeOgImg(values) {
      if (isNotEmpty(values)) {
        this.ogImg = values.name;
        this.ogImgSize = values.size;
      }
    },
    /**    */
    listCategory() {
      return this.$http.get('/category')
      .then(res => {
        res.data.map(d => {
          this.categoryList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /**    */
    listTag() {
      return this.$http.get('/tag')
      .then(res => {
        res.data.map(d => {
          this.tagList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /**    */
    onChangeTag(value) {
      const foundTag = this.tagList.find(d => d.value == value).text;

      if (isBlank(this.tagStr)) {
        this.tagStr = foundTag;
      } else {
        this.tagStr += `, ${foundTag}`;
      }
    },
    /**    */
    setTagArr(formValues) {
      if (isBlank(this.tagStr)) {
        return;
      }

      this.saveTagList = [];

      const tagArr = this.tagStr.split(',');

      for (let i = 0; i < tagArr.length; i++) {
        this.saveTagList.push({
          idx: i+1, //  index. HTTP   
          name: tagArr[i].trim(),
          addTagYn: 'Y',
        });
      }

      formValues['saveTagList'] = this.saveTagList;
    },
    /**   */
    validationCheck() {
      const tagArr = this.tagStr.split(',');

      if (arrayHasDuplicateValue(tagArr)) {
        messageUtil.toastWarning('  .');
        return false;
      }

      if (5 < tagArr.length) {
        messageUtil.toastWarning(' 5   .');
        return false;
      }

      return true;
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/views/write.scss';
</style>